<template>
<div class="bottom-menu">
  <mu-paper>
    <mu-bottom-nav :value="activeTab" @change="handleTabChange">
      <mu-bottom-nav-item value="home" to="/home/index" title="首页" icon="home"/>
      <mu-bottom-nav-item value="product" to="/home/product" title="产品" icon="description"/>
      <mu-bottom-nav-item value="serive" to="/home/index" title="服务" icon="favorite"/>
      <mu-bottom-nav-item value="member" to="/home/index" title="我的" icon="face"/>
    </mu-bottom-nav>
  </mu-paper>
</div>
</template>
<script>
  export default {
    data(){
      return {
        activeTab: 'home',
        activeClass:'active'
      }
    },
    methods:{
      handleTabChange (val){
        let path=this.$route.path;
        this.activeTab = val;
      }
    }
  }
</script>
<style lang="stylus" scoped>
  .active
    color red
  .nomal
    color yellow
</style>



